@import "./variables.scss";
@import "./mixins.scss";


* {
    padding : 0;
    margin : 0;
    box-sizing: border-box;
}

ul {
    li {
        list-style-type: none;
    }
}

input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
  -webkit-appearance: none; 
  margin: 0; 
}

input[type="number"] {
    -moz-appearance: none;
}

section {
    min-height: 100vh;
    padding : 1rem 1.5rem;

    .title-content {
        @include font-scale(2.4rem);
        margin-bottom : 1rem;
        padding-right: 2rem;
        display: inline-block;
        border-bottom : 3px solid $base-color-pink;
    }

    .content-wrapper {
        position: relative;
        display: flex;
        flex-wrap : wrap;
        justify-content: space-between;
    }

    .card-cart-wrapper {
        @include respond("desktio") {
            flex : 0 0 55%;
            margin-right: 2rem;
        }
        margin-top : 1rem;
    
        .card {
            position: relative;
            padding : 1rem 1rem;
            padding-top : 1.5rem;
            margin-bottom : 2rem;
            display: flex;
            border-radius: 15px;
            background-color : #303030;
            color : white;
            transition: .4s ease;
            box-shadow: 1px 0px 7px rgba(34, 34, 34, 0.616);
        
            .img-wrapper {
                flex : 0 0 45%;
                display: flex;
                align-items: flex-start;

                img {
                    width: 100%;
                }
            }

            .card-details {
                flex : 0 0 55%;
                padding : .5rem 1rem;
                padding-right: 0;

                .top-detail {
                    margin-bottom : 1.3rem;
                    display: flex;
                    justify-content: flex-start;

                    h3 {
                        margin-right : 1rem;
                    }
                }

              

                .date-wrapper {
                    margin-bottom : 1rem;
                    display: block;

                    h4 {
                        margin-right : 1rem;
                        margin-bottom : .5rem;
                        font-size : 1.05rem;
                    }

                    .duration {
                        display  : flex;
                        margin : .7rem 0rem;

                        input[type="number"] {  
                            text-align: center;  
                            width : 3.5rem;
                            font-size : 1.05rem;
                            padding :  0rem .5rem;
                            border : none;
                            outline : none;
                            background-color : transparent;
                            color : white;
                        }

                        .changes-duration {
                            width : 1.4rem;
                            background-color : $base-color-pink;
                            color : white;
                            border-radius : 50%;
                        }

                        .changes-duration:hover {
                            background-color : $base-color-dark-pink;
                        }

                        .delete-cart {
                            font-size : 1rem;
                            background-color : transparent;
                            margin-left : .75rem;
                            padding-left : .75rem;
                            color : #dc3535;
                            font-weight : 500;
                            border-left : 2px solid white;
                        }
                    }


                }

                .list-tag {
                    margin-bottom : 1rem;
                    display: flex;
                    flex-wrap : wrap;
                    justify-content: flex-start;
                    
                    .tag-item  {
                        cursor : pointer;
                        background-color: #3b86db;
                        margin-right : .5rem;
                        color : #fafafa;
                        margin-bottom : .75rem;
                        padding: .3rem .7rem;
                        border-radius : 5px;
                    }
                }

                .sub-price {
                    letter-spacing: 2px;
                }

                .btn-rent-now {
                    margin-top : 1rem;
                    padding : .4rem .9rem;
                    border : none;
                    outline : none;
                    background-color : $base-color-pink;
                    color : white;
                    border-radius : 3px;
                    transition: all .3s ease;
                }

                .btn-rent-now:hover {
                    background-color : $base-color-dark-pink;
                }
            
            }

            .select-wrapper {
                cursor: pointer;
                position : absolute;
                top : 1rem;
                left : 1rem;
                display: flex;
                align-items : center;

                input[type="checkbox"] {
                    width: 1.2rem;
                    height : 1.2rem;
                    cursor : pointer;
                }

            }
        }


        .selected-cart {
            background-color: $base-color-pink;


            .card-details {

                .btn-rent-now {
                    background-color : #222222;
                }

                .btn-rent-now:hover {
                    background-color : #0e0e0e;
                }
                

                .date-wrapper {
                    .duration {

                        .changes-duration {
                            background-color : #303030;
                        }
                    }
                }

                        
                .list-tag {
                
                    
                    .tag-item  {
                        background-color: #206ec7;
                    }
                }
            }
        }
    }
}



.result-wrapper {
    position: -webkit-sticky;
    position: sticky;
    top  : 0;
    align-self : flex-start;
    flex :  0 0 40%;
    margin-top : 1rem;
    width: 100%;
    background-color : $base-color-dark-blue;
    color : white;
    padding : 2rem 2rem;
    padding-bottom : 3rem;
    border-radius : 15px;
    box-shadow: 1px 0px 7px rgba(34, 34, 34, 0.616);

    @include respond("mobile") {
        flex : 0 0 100%;
    }

    .title-header {
        margin-bottom : 1.5rem;
    }

    .order-count-wrapper,
    .total-price-wrapper  {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 1rem;
    }
}



.btn-pay-order {
    margin-top : 1rem;
    width: 100%;
    color : white;
    padding : .4rem .9rem;
    border : none;
    outline : none;
    background-color: $base-color-pink;
    box-shadow: 1px 0px 4px rgba(71, 71, 71, 0.37);
    border-radius : 5px;
    display: block;
    font-size: 1.1rem;
    font-weight : 600;
}

.btn-pay-order:hover {
    background-color: $base-color-dark-pink;
}
